.header {
	&:before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;

		width: 100%;
		height: 100%;

		background-color: $white-color;

		z-index: 3;
	}
	position: fixed;
	top: 0;
	left: 0;

	width: 100%;

	z-index: 3;

	color: $gray-color;
	&__body {
		position: relative;
		z-index: 3;

		display: flex;
		justify-content: space-between;
		align-items: center;

		padding: 16px;
	}

	&__logo {
		margin-right: 34px;
	}
	&__burger {
		display: none;
		flex: 0 0 auto;
	}
	&__menu {
		position: relative;

		width: 100%;

		display: flex;
		align-items: center;
		justify-content: space-between;
		@media (max-width: 1200px) {
			flex-wrap: wrap;
		}
		@media (max-width: $md2+px) {
			flex-direction: column;
			justify-content: flex-start;
			flex-wrap: nowrap;
		}
	}
	&__form {
		flex: 0 1 auto;
		margin: 0px 29px 0px 0px;
		@media (max-width: 1919px) {
			margin: 0px 15px 0px 0px;
		}
		@media (max-width: 1200px) {
			order: 2;
			margin: 10px 0px 0px 0px;
		}
		@media (max-width: $md2+px) {
			margin: 0px 0px 0px 0px;
		}
	}

	&__link {
		flex: 0 0 auto;

		position: relative;

		display: inline-block;

		color: $gray-color;

		font-weight: 400;
		font-size: 20px;
		line-height: 158%;
		text-align: center;

		transition: color 0.3s ease;

		&--medium {
			margin-right: 34px;

			font-weight: 500;
		}
		&--reg {
			margin-right: 109px;
			@media (max-width: 1919px) {
				margin: 0px 15px 0px 0px;
			}
		}
		&--consumer {
			margin-right: 128px;
			@media (max-width: 1919px) {
				margin: 0px 15px 0px 0px;
			}
		}
		&--profile {
			display: inline-block;
			@media (min-width: $md3+px) {
				&:hover .header__profile-image {
					transform: scale(1.1);

					transition: transform 0.2s ease;
				}
				&:focus .header__profile-image {
					transform: scale(1.1);

					transition: transform 0.2s ease;
				}
			}
			@media (max-width: $md2+px) {
				position: absolute;
				top: 10px;
				left: 10px;
			}
		}
		@media (min-width: $md3+px) {
			&:hover {
				color: $blue-color;
				transition: color 0.3s ease;
			}
			&:focus {
				color: $blue-color;
				transition: color 0.3s ease;
			}
		}
		@media (max-width: $md1+px) {
			font-size: 16px;
		}
		@media (max-width: $md2+px) {
			margin: 0px 0px 20px 0px;

			font-size: 36px;
		}
		@media (max-width: $md3+px) {
			font-size: 24px;
		}
	}
	&__profile-image {
		width: 40px;
		height: 40px;

		object-fit: cover;

		border-radius: 50%;

		transition: transform 0.3s ease;
		@media (max-width: $md2+px) {
			width: 50px;
			height: 50px;
		}
	}
	&__btn {
		flex: 0 0 auto;

		&--sign {
			margin-right: 20px;
			@media (max-width: 1919px) {
				margin: 0px 15px 0px 0px;
			}
		}
		&--cart {
			position: relative;
			padding: 4px 13px;

			margin-right: 14px;
			@media (max-width: 1919px) {
				margin: 0px 15px 0px 0px;
			}
			@media (max-width: 1200px) {
			}
		}
		@media (max-width: $md1+px) {
			font-size: 16px;
		}
		@media (max-width: $md2+px) {
			margin: 0px 0px 20px 0px;

			font-size: 36px;
		}
		@media (max-width: $md3+px) {
			font-size: 24px;
		}
	}

	&__cart-quantity {
		position: absolute;
		top: 0;
		right: 0;

		display: flex;
		align-items: center;
		justify-content: center;

		width: 20px;
		height: 20px;

		background-color: red;
		border-radius: 50%;
		color: $white-color;

		font-size: 14px;

		transform: translate(2px, -4px);
		@media (max-width: $md1+px) {
			transform: translate(4px, -10px);
		}
	}
	@media (max-width: $md2+px) {
		&__burger {
			display: block;
			position: relative;
			width: 40px;
			height: 20px;
			z-index: 4;
			position: relative;
		}
		&__burger span {
			position: absolute;
			width: 100%;
			height: 2px;
			background-color: #000;
			left: 0;
			top: 9px;
			transition: transform 0.2s ease 0s;
		}
		&__burger:before,
		&__burger:after {
			content: "";
			position: absolute;
			left: 0;

			width: 100%;
			height: 2px;

			background-color: #000;

			transition: all 0.2s ease 0s;
		}
		&__burger:before {
			top: 0;
		}
		&__burger:after {
			bottom: 0;
		}
		&__burger.active span {
			transform: scale(0);
		}
		&__burger.active:before {
			top: 9px;

			transform: rotate(45deg);
		}
		&__burger.active:after {
			bottom: 9px;

			transform: rotate(-45deg);
		}
		&__menu {
			position: fixed;
			top: 0;
			left: 0;
			z-index: 3;

			width: 100%;
			height: 100%;

			padding: 130px 10px 0px 10px;

			overflow: auto;

			background-color: $white-color;
			box-shadow: 0px 10px 8px 0px rgba(34, 60, 80, 0.2);

			transform: translateY(-101%);
			transition: transform 0.3s ease 0s;
		}
		&__menu.active {
			transform: translateY(0%);
		}
		&__list {
			display: block;
		}
		&__link {
		}
	}
}

.logo {
	order: 0;
	display: flex;
	align-items: center;

	color: $blue-zodiac-color;

	font-weight: 500;
	font-size: 36px;
	line-height: 36px / 36px * 100%;
	@media (max-width: $md1+px) {
		font-size: 20px;
	}
	&__image {
		width: 40px;
		height: 40px;

		margin-right: 12px;

		object-fit: cover;
	}
}

.search-form {
	position: relative;

	width: 100%;
	max-width: 668px;
	@media (max-width: 1200px) {
		max-width: 100%;
	}
	@media (max-width: $md2+px) {
		position: absolute;
		top: 70px;
		left: 0;

		width: 100%;
		padding: 0 15px;
	}
	&__input {
		width: 100%;

		padding: 8px 44px;

		background-color: $zhumtor-color;
		border-radius: 99px;
		border: 1px solid transparent;
		color: $shark-color;

		font-size: 20px;
		line-height: 158%;

		transition: background-color 0.3s ease, border 0.3s ease;
		@media (min-width: $md3+px) {
			&:hover {
				background-color: $athens-gray-color;
			}
			&:focus {
				background-color: $athens-gray-color;
				outline: 0;
				border: 1px solid $gray-color;
			}
		}
		@media (max-width: $md1+px) {
			font-size: 16px;
		}
	}

	&__submit {
		position: absolute;
		top: 50%;
		left: 15px;

		width: 17.5px;
		height: 17.5px;

		background: url(../img/search-icon.svg) center/cover no-repeat;

		will-change: transform;
		transform: translateY(-50%);
		transition: transform 0.2s ease;
		@media (min-width: $md3+px) {
			&:hover {
				transform: translateY(-50%) scale(1.1);
				transition: transform 0.2s ease;
			}
			&:focus {
				transform: translateY(-50%) scale(1.1);
				transition: transform 0.2s ease;
			}
		}
		@media (max-width: $md2+px) {
			left: 30px;
		}
	}

	&__reset {
		position: absolute;
		top: 50%;
		right: 17px;

		visibility: hidden;

		width: 14px;
		height: 14px;

		background: url(../img/x-icon.svg) center/cover no-repeat;
		opacity: 0;

		will-change: transform;
		transform: translateY(-50%);
		transition: opacity 0.2s ease, visibility 0.2s ease, transform 0.2s ease;

		&--visible {
			visibility: visible;
			opacity: 1;

			transition: opacity 0.2s ease, visibility 0.2s ease;
		}
		@media (max-width: $md2+px) {
			right: 30px;
		}
		@media (min-width: $md3+px) {
			&:hover {
				transform: scale(1.1) translateY(-50%);
				transition: transform 0.2s ease;
			}
			&:focus {
				transform: scale(1.1) translateY(-50%);
				transition: transform 0.2s ease;
			}
		}
	}
}
