// Common
.visually-hidden {
	position: absolute !important;
	clip: rect(1px 1px 1px 1px); /* IE6, IE7 */
	clip: rect(1px, 1px, 1px, 1px);

	height: 1px !important;
	width: 1px !important;

	padding: 0 !important;
	border: 0 !important;

	overflow: hidden;
}
// Reset classes
.btn-reset {
	width: auto;

	margin: 0;
	padding: 0;

	background: transparent;
	color: inherit;

	font-family: inherit;

	font: inherit;
	cursor: pointer;
	-webkit-font-smoothing: inherit;
	-moz-osx-font-smoothing: inherit;
	-webkit-appearance: none;
	&::-moz-focus-inner {
		border: 0;
		padding: 0;
	}
	&:focus:not(:focus-visible) {
		outline: none;
	}
	@media (max-width: $md3+px) {
		outline: none;
	}
	@extend %reset;
}
.link-reset {
	color: inherit;

	text-decoration: none;
	&:visited {
		text-decoration: none;
	}
	&:hover {
		text-decoration: none;
	}
	&:focus:not(:focus-visible) {
		outline: none;
	}
	@media (max-width: $md3+px) {
		outline: none;
	}
	@extend %reset;
}
.list-reset {
	text-indent: 0;
	list-style: none;
	list-style-type: none;
	@extend %reset;
}
.input-reset {
	border: 0;
	font-family: inherit;
	&::-ms-clear {
		display: none;
	}
	&::-webkit-search-cancel-button,
	&::-webkit-search-decoration {
		-webkit-appearance: none;
		appearance: none;
	}
	&:focus:not(:focus-visible) {
		outline: none;
	}
	@media (max-width: $md3+px) {
		outline: none;
	}
}

// -- //

// Main classes
.text {
	font-weight: 400;
	font-size: $fz-text;
	line-height: 1.1;
	@media (max-width: $md2+px) {
		font-size: 16px;
	}
	@media (max-width: $md3+px) {
		font-size: 16px;
	}
	@media (max-width: $md4+px) {
		font-size: 16px;
	}
}

.main-heading {
	font-size: $fz-heading;
	font-weight: 700;
	@media (max-width: $md1+px) {
		font-size: 45px;
	}
	@media (max-width: $md2+px) {
		font-size: 35px;
	}
	@media (max-width: $md3+px) {
		font-size: 30px;
	}
	@media (max-width: $md4+px) {
		font-size: 20px;
	}
}
.main-btn {
	padding: 4px 17px;

	border: 1px solid $anakiwa-color;
	box-sizing: border-box;
	border-radius: 4px;
	color: $blue-color;

	font-weight: 700;
	font-size: 20px;
	line-height: 150%;

	transition: background-color 0.3s ease, box-shadow 0.3s ease, border-radius 0.3s ease;

	@media (min-width: $md3+px) {
		&:hover {
			box-shadow: 0px 2px 4px rgba(27, 78, 163, 0.2), 0px 4px 8px rgba(41, 121, 255, 0.2);
			border-radius: 4px;
			background: $white-color;

			transition: background-color 0.3s ease, box-shadow 0.3s ease, border-radius 0.3s ease;
		}
		&:focus {
			box-shadow: 0px 2px 4px rgba(27, 78, 163, 0.2), 0px 4px 8px rgba(41, 121, 255, 0.2);
			border-radius: 4px;
			background: $white-color;

			transition: background-color 0.3s ease, box-shadow 0.3s ease, border-radius 0.3s ease;
		}
	}
}
.link-line {
	position: relative;

	display: inline-block;
	overflow: hidden;

	color: $blue-color;

	@media (min-width: $md3+px) {
		&::after {
			content: "";
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			height: 2px;
			background-color: $blue-color;
			transform: translate(-101%);
			transition: transform 0.3s ease;
		}
		&:hover::after {
			transform: translateX(0%);
			transition: transform 0.3s ease;
		}
		&:focus::after {
			transform: translateX(0%);
			transition: transform 0.3s ease;
		}
	}
}
// -- //
