  position: absolute;
  top: 50%;
  right: 17px;
  visibility: hidden;
  width: 14px;
  height: 14px;
  background: url(../img/x-icon.svg) center/cover no-repeat;
  opacity: 0;
  will-change: transform;
  transform: translateY(-50%);
  transition: opacity 0.2s ease, visibility 0.2s ease, transform 0.2s ease;
}

.search-form__reset--visible {
  visibility: visible;
  opacity: 1;
  transition: opacity 0.2s ease, visibility 0.2s ease;
}

@media (max-width: 991.98px) {
  .search-form__reset {
    right: 30px;
  }
}

@media (min-width: 767.98px) {
  .search-form__reset:hover {
    transform: scale(1.1) translateY(-50%);
    transition: transform 0.2s ease;
  }
  .search-form__reset:focus {
    transform: scale(1.1) translateY(-50%);
    transition: transform 0.2s ease;
  }
}

.snackbar {
  position: fixed;
  bottom: 30px;
  right: 50px;
  z-index: 1;
  width: auto;
  min-width: 200px;
  padding: 15px;
  visibility: hidden;
  border-radius: 2px;
  color: #fff;
  text-align: center;
}

@media (max-width: 767.98px) {
  .snackbar {
    right: 50%;
    transform: translateX(50%);
  }
}

.snackbar.show-notification {
  visibility: visible;
  background-color: #333;
  /* Если 3000 в js, то в стилях 2.6s */
  animation: fadein 0.5s, fadeout 0.5s 2.6s;
}

.snackbar.show-attention {
  visibility: visible;
  background-color: #ff6060;
  /* Если 3000 в js, то в стилях 2.6s */
  animation: fadein 0.5s, fadeout 0.5s 2.6s;
}

@keyframes fadein {
  from {
    bottom: 0;
    opacity: 0;
  }
  to {
    bottom: 30px;
    opacity: 1;
  }
}

@keyframes fadeout {
  from {
    bottom: 30px;
    opacity: 1;
  }
  to {
    bottom: 0;
    opacity: 0;
  }
}

/* Modal */
/*  Объект для модального окна, чтобы страница не прокручивалась вверх  */
.fixed {
  position: fixed;
  top: 0;
  left: 0;
  opacity: 0;
}

/* ----- */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  opacity: 0;
  visibility: hidden;
  backface-visibility: hidden;
  background-color: rgba(0, 0, 0, 0.726);
  transition: opacity 0.7s ease, visibility 0.7s ease;
}

.modal.open {
  opacity: 1;
  visibility: visible;
}

.modal.open .modal__content {
  opacity: 1;
  transform: translate(0px, 0px);
}

.modal__wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 90vh;
  padding: 30px 10px;
}

.modal__content {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 60px;
  border-radius: 30px;
  background-color: #f2eef7;
  opacity: 0;
  color: black;
  transform: translate(0px, -100%);
  transition: opacity 0.7s ease, transform 0.7s ease;
}

.modal__close {
  position: absolute;
  top: 0;
  right: 15px;
  display: block;
  align-self: flex-end;
  background: transparent;
  color: black;
  font-size: 32px;
  transition: all 0.2s ease-in;
}

.modal__close:hover {
  transform: scale(1.2);
}

.modal__title {
  margin-bottom: 20px;
  color: #2979ff;
  font-weight: 500;
  font-size: 36px;
  line-height: 100%;
  text-align: center;
}

.modal__close-image {
  width: 15px;
}

.sign-form {
  display: flex;
  flex-direction: column;
}

.sign-form__fieldset {
  display: flex;
  flex-direction: column;
}

.sign-form__legend {
  margin-bottom: 15px;
  color: #2979ff;
  font-weight: 500;
  font-size: 26px;
  text-align: center;
}

.sign-form__label {
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  font-size: 20px;
}

.sign-form__label:not(:last-child) {
  margin-bottom: 20px;
}

.sign-form__label:last-child {
  margin-bottom: 5px;
}

@media (max-width: 767.98px) {
  .sign-form__label {
    flex-direction: column;
    justify-content: center;
  }
}

.sign-form__input {
  width: 70%;
  padding: 5px 10px;
  border-radius: 20px;
  font-size: 20px;
  transition: background-color 0.3s ease, color 0.3s ease;
}

@media (max-width: 767.98px) {
  .sign-form__input {
    width: 100%;
  }
}

@media (min-width: 767.98px) {
  .sign-form__input:hover {
    color: #fff;
    background-color: #85a1fd;
    outline: none;
    transition: background-color 0.3s ease, color 0.3s ease;
  }
  .sign-form__input:focus {
    color: #fff;
    background-color: #85a1fd;
    outline: none;
    transition: background-color 0.3s ease, color 0.3s ease;
  }
}

.sign-form__link {
  align-self: flex-end;
  padding-bottom: 5px;
  margin-bottom: 5px;
}

.sign-form__label-checkbox {
  align-self: flex-start;
  margin-bottom: 20px;
  user-select: none;
  cursor: pointer;
}

.sign-form__checkbox {
  cursor: pointer;
}

.sign-form__sumbit {
  align-self: center;
  margin-bottom: 20px;
}

.sign-form__reg {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  text-align: center;
}

