/* Modal */
/*  Объект для модального окна, чтобы страница не прокручивалась вверх  */
.fixed {
	position: fixed;
	top: 0;
	left: 0;

	opacity: 0;
}
/* ----- */
.modal {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 10;

	width: 100%;
	height: 100%;

	overflow-y: auto;
	overflow-x: hidden;

	opacity: 0;
	visibility: hidden;
	backface-visibility: hidden;
	background-color: rgba(0, 0, 0, 0.726);

	transition: opacity 0.7s ease, visibility 0.7s ease;

	&.open {
		opacity: 1;
		visibility: visible;
	}
	.modal.open &__content {
		opacity: 1;
		transform: translate(0px, 0px);
	}

	&__wrapper {
		display: flex;
		align-items: center;
		justify-content: center;

		min-height: 90vh;
		padding: 30px 10px;
	}

	&__content {
		position: relative;

		display: flex;
		flex-direction: column;

		padding: 60px;

		border-radius: 30px;
		background-color: #f2eef7;
		opacity: 0;
		color: black;

		transform: translate(0px, -100%);
		transition: opacity 0.7s ease, transform 0.7s ease;
	}

	&__close {
		position: absolute;
		top: 0;
		right: 15px;

		display: block;
		align-self: flex-end;

		background: transparent;
		color: black;

		font-size: 32px;
		transition: all 0.2s ease-in;
		&:hover {
			transform: scale(1.2);
		}
	}
	&__title {
		margin-bottom: 20px;

		color: $blue-color;

		font-weight: 500;
		font-size: 36px;
		line-height: 36px / 36px * 100%;
		text-align: center;
	}

	&__close-image {
		width: 15px;
	}

	&__body {
	}
}
// -- //

.sign-form {
	display: flex;
	flex-direction: column;
	&__fieldset {
		display: flex;
		flex-direction: column;
	}
	&__legend {
		margin-bottom: 15px;

		color: $blue-color;

		font-weight: 500;
		font-size: 26px;
		text-align: center;
	}
	&__label {
		display: flex;
		align-items: center;
		justify-content: space-between;

		cursor: pointer;

		&:not(:last-child) {
			margin-bottom: 20px;
		}
		&:last-child {
			margin-bottom: 5px;
		}
		font-size: 20px;
		@media (max-width: $md3+px) {
			flex-direction: column;

			justify-content: center;
		}
	}

	&__input {
		width: 70%;

		padding: 5px 10px;

		border-radius: 20px;

		font-size: 20px;
		transition: background-color 0.3s ease, color 0.3s ease;

		@media (max-width: $md3+px) {
			width: 100%;
		}
		@media (min-width: $md3+px) {
			&:hover {
				color: $white-color;
				background-color: rgb(133, 161, 253);
				outline: none;
				transition: background-color 0.3s ease, color 0.3s ease;
			}
			&:focus {
				color: $white-color;
				background-color: rgb(133, 161, 253);
				outline: none;
				transition: background-color 0.3s ease, color 0.3s ease;
			}
		}
	}

	&__link {
		align-self: flex-end;

		padding-bottom: 5px;
		margin-bottom: 5px;
	}
	&__label-checkbox {
		align-self: flex-start;

		margin-bottom: 20px;

		user-select: none;

		cursor: pointer;
	}
	&__checkbox {
		cursor: pointer;
	}

	&__sumbit {
		align-self: center;
		margin-bottom: 20px;
	}

	&__reg {
		display: flex;
		align-items: center;
		justify-content: center;

		font-size: 20px;
		text-align: center;
	}
}
